import { fork } from "redux-saga/effects";
import * as studentSearchPageCommands from './studentSearchPageCommands'
import { watchCommandChapter } from "../../common/utilities/createCommand";
import * as authenticationCommands from "./authentication";

export const studentRootSaga = function*() {
  yield fork(watchCommandChapter(authenticationCommands));
  yield fork(watchCommandChapter(studentSearchPageCommands));
};
